.progress{
    width: 65%;
    margin: 40px auto;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 35px;
}
.ui-widgets { 
	position: relative; 
	display: inline-block; 
	width: 155px; 
	height: 155px; 
	border-radius: 50%; 
	border: 20px solid palegreen; 
	box-shadow: inset 0 0 7px grey; 
	border-left-color: palegreen; 
	border-top-color:chartreuse ; 
	border-right-color: darkgreen; 
	border-bottom-color: white ; 
	text-align: center; 
	box-sizing: border-box; 
} 

.ui-widgets:nth-child(n+4) { 
	border-top-color:chartreuse ; 
	border-right-color: white; 
	border-left-color: palegreen; 
	border-bottom-color: white; 
} 


.ui-widgets .ui-values { 
	top: 35px; 
	position: absolute; 
	left: 10px; 
	right: 0; 
	font-weight: 700; 
	font-size: 2.0rem; 
} 

.ui-widgets .ui-labels { 
	left: 0; 
	bottom: -17px; 
	text-shadow: 0 0 4px grey; 
	color:black; 
	position: absolute; 
	width: 100%; 
	font-size: 16px; 
}

@media screen and (max-width: 500px) {
    .progress{
        width: 95%;
    }
}
